<template>
	<div class="tnav">
		<div class="logo">
			<a href="javascript:;">
				<img src="./../assets/logo.png" height="60" width="109" alt="首页logo">
			</a>
		</div>
		<div class="index_nav">
			<router-link to="/">首页</router-link>
			<router-link to="/viewcolor">颜色</router-link>
			<router-link to="/viewnumber">数字</router-link>
		</div>
	</div>
</template>

<script>
	export default{
		name: 'TpsonNav',
		data: function(){
			return {
				tnav: 'msg'
			}
		}
	}
</script>

<style scoped>
	.tnav{
		display: flex;
		justify-content: space-between;
		height: 60px;
		background-color: transparent;
	}
</style>


