import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ViewIndex from '@/components/ViewIndex'
import ViewColor from '@/components/ViewColor'
import ViewNumber from '@/components/ViewNumber'
import IndexOne from '@/components/IndexOne'
import IndexTwo from '@/components/IndexTwo'
import IndexThree from '@/components/IndexThree'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ViewIndex',
      component: ViewIndex,
      children: [
      	{
      		path: '',
      		name: 'IndexOne',
      		component: IndexOne
      	},
      	{
      		path: 'indexone',
      		name: 'IndexOne',
      		component: IndexOne
      	},
      	{
      		path: 'indextwo',
      		name: 'IndexTwo',
      		component: IndexTwo
      	},
      	{
      		path: 'indexthree',
      		name: 'IndexThree',
      		component: IndexThree
      	}
      ]
    },
    {
      path: '/viewcolor',
      name: 'ViewColor',
      component: ViewColor
    },
    {
      path: '/viewnumber',
      name: 'ViewNumber',
      component: ViewNumber
    }
  ]
})



