<template>
	<div class="indextwo">
		<router-link to="/indexone">首页1111</router-link>
		<router-link to="/indextwo">首页2222</router-link>
		<router-link to="/indexthree">首页3333</router-link><br>
		首页222222，鼠标滚动时触发函数，改变路由
	</div>
</template>

<script>
	export default{
		name: 'IndexTwo',
		data: function(){
			return {

			}
		},
		mounted() {
			/*注册事件*/
			if(document.addEventListener){
			    document.addEventListener('DOMMouseScroll',this.scrollFunc,false);
			}//W3C
			window.onmousewheel=document.onmousewheel=this.scrollFunc;//IE/Opera/Chrome/Safari
			/*window.addEventListener('scroll', this.handleScroll)*/
		},
		methods: {
			scrollFunc(e){
				console.log('in scrollFunc');
			    var direct=0;
			    e=e || window.event;
			   
			    if(e.wheelDelta){//IE/Opera/Chrome
			        console.log(e.wheelDelta);
			        if(e.wheelDelta < 0){
			        	this.$router.push('/indexthree');
			        }else{
			        	this.$router.push('/indexone');
			        }
			    }else if(e.detail){//Firefox
			        console.log(e.detail);
			    }
			    //ScrollText(direct);
			}
			/*handleScroll () {
			  	var scrollTop = document.documentElement.scrollTop ||window.pageYOffset || document.body.scrollTop
			  	console.log(scrollTop)
			}*/

		}
	}
</script>

<style>
	
</style>


